<template>
    <div :class="['player',{pause:!playstate}]">
        <audio controls autoplay :src="`https://music.163.com/song/media/outer/url?id=${playMusic.id}.mp3`" 
        v-if="playMusic"
        @timeupdate="timeupdate"
        @pause="pause"
        @play="play"
        @ended="end"
        ref="player"
        ></audio>
    <transition enter-active-class="animate__animated animate__slideInUp" leave-active-class="animate__animated animate__slideOutDown">
        <div class="minibar" v-if="playMusic">
            <div><img :src="playMusic.picUrl"/></div>
            <div @click="isFull=true">
                <p class="text-hidden">{{playMusic.name}}</p>
                <p class="text-hidden">专辑:{{playMusic.song.album.name}}</p>
                <p>歌手:<span v-for="art in playMusic.song.artists"
                    :key="art.id"
                    >{{art.name}}</span></p>
            </div>
            <span @click.stop="prev" class="btn1 iconfont icon-shangyishou"></span>
            <span @click.stop="next" class="btn2 iconfont icon-xiayishou"></span>

            <div  @click.stop="toggoplay">
                <canvas width="60" height="60" ref="playround" class="nb1"></canvas>
                <span v-if="!playstate" class="nb iconfont icon-bofang1"></span>
                <span v-if="playstate" class="nb iconfont icon-zanting1"></span>
            </div>


        </div>
    </transition>

       <transition  enter-active-class="animate__animated animate__slideInUp" leave-active-class="animate__animated animate__slideOutRight">

            <Bofang v-if="isFull" 
            @close="isFull=false" 
            :playMusic="playMusic"
            :playstate="playstate"
            :lyric="lyric"
            :currentTime="currentTime"
            :duration="duration"
            @changetime="changeTime"
            @toggoPlay="toggoplay"
            :playList="playList"
            :playMode="playMode"
            @changePlayMusic="$emit('changePlayMusic',$event)"
            @changeMode="playMode=$event"

            ref="bofang"
            ></Bofang>
        </transition>
    </div>

</template>
<script>
import Bofang from '@/components/Bofang'
export default {
    props:["playMusic","playList"],
    data(){
        return{
            duration:0,
            currentTime:0,
            playstate:true,
            guanbi:true,
            isFull:false,
            lyric:null,
            playMode:1,

        }
    },
    components:{
        Bofang
    },
    watch:{
        playMusic(newMusic){
            //侦听playMusic当前播放的音乐.获取 歌词
            this.axios.get('/lyric',{
                params:{
                    id:newMusic.id
                }
            }).then(d=>{
                if(d.data&&d.data.lrc&&d.data.lrc.lyric){
                    this.lyric=d.data.lrc.lyric;
                }
            });
        }
    },
    methods:{
        end(){
            
            if(this.playMode==3){//如果播放模式 是单曲循环 继续播放当前歌曲
                this.$refs.player.play();
            }else{
                //当前音乐播放完成 播放下一曲 调用子组件的next函数.
                this.$refs.bofang.next();
            }

        },
        timeupdate(event){
            this.duration=event.target.duration;
            this.currentTime=event.target.currentTime;
            this.drawCircle(this.duration,this.currentTime);
        },
        changeTime(val){
            let t = val/100 * this.duration;
            this.$refs.player.currentTime=t;
        },
        timeupdatechild(event){
            let ctime = event.target.currentTime;
            let lyricList=this.$refs.lyriclist;
            this.transDistance=lyricList[this.index].offsetTop;
            if(this.index>=this.lyricArr.length-2){
                this.index=this.lyricArr-1;
                return;
            }
            for(let i=0;i<this.lyricArr.length;i++){
                if(this.lyricArr[i].time < ctime && this.lyricArr[i+1].time > ctime){
                    this.index = i;
                    break;
                }
            }
        },       
        drawCircle(total,current){
            let canvas = this.$refs.playround;
            let cxt=canvas.getContext('2d');
                cxt.clearRect(0,0,50,50);
                cxt.beginPath();
                cxt.strokeStyle="#023a67";
                cxt.lineWidth=3;
                cxt.arc(25,25,20,0,Math.PI*2);
                cxt.stroke();
                cxt.closePath();

                cxt.beginPath();
                cxt.strokeStyle="white";
                cxt.lineWidth=3;
                cxt.arc(25,25,20,-0.5*Math.PI,((2 * (current/total))-0.5)*Math.PI);
                cxt.stroke();
                cxt.closePath();
        },
        toggoplay(){
            if(this.playstate){
                this.$refs.player.pause();
            }else{
                this.$refs.player.play();
            }
        },
        play(){
                this.playstate=true;
        },
        pause(){
                this.playstate=false;
        },
        prev(){
            let i=0;
            this.playList.map((d,index)=>{
                if(d.id==this.playMusic.id){//找到当前播放音乐下标
                        i=index;
                }
            });
            if(this.playMode==1){
                i--;//上一曲 判断是不是第一曲
            }else if(this.playMode==2){
                i=Math.floor(Math.random()*this.playList.length);
            }


            let pmusic=this.playList[i];
            this.$emit('changePlayMusic',pmusic);
            //$emit('',song)

        },
        next(){
           let i=0;
           this.playList.map((d,index)=>{
               if(d.id==this.playMusic.id){//找到当前播放音乐下标
                    i=index;
               }
           });
            if(this.playMode==1){
                i++;//下一曲 判断是不是最后一首 
            }else if(this.playMode==2){
                i=Math.floor(Math.random()*this.playList.length);
            }
           let pmusic=this.playList[i];
           this.$emit('changePlayMusic',pmusic);

        }
    }
    
}
</script>
<style lang="less" scoped>
audio{
    // margin-top: 100px;
    // height: 100px;

}
.nb1{
    position: relative;
    z-index: 99;
}
.player{
    // background-color: chartreuse;
    height: 0;
    .minibar{
        position: fixed;
        padding-left: 20px;
        height: 80px;
        color: rgb(255, 255, 255);
        background-color: #023a67;
        width:100%;
        bottom: 0px;
        z-index: 9999;
        display: flex;
        align-items: center;
        div{
            // padding: 0 10px;
            margin-right: 20px;
            &:first-child{
                padding: 0;
                width: 50px;
                height: 50px;
                img{
                    width:100%;
                    border-radius: 50%;
                    animation: round 5s 0s linear infinite;
                    // animation-play-state: running;

                }
            }
            &:nth-child(2){
                flex:1;
                // text-indent: 20px;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
                overflow: hidden;

            }
            &:last-child{
                width: 50px;
                height: 50px;
                margin-right: 50px;
                font-size: 12px;
                position: relative;
                .nb{
                    color: white;
                    font-size: 42px;
                
                    width: 38px;
                    height: 38px;
                    position: absolute;
                    top:5px;
                    left: 4px;
                    border-radius: 50%;
                    background: #023a67;
                }
                // &::before{
                //     content:" (" attr(url(../assets/123.jpg)) ")";
                //     position: absolute;
                //     height: 100%;
                //     width: 100%;
                //     // text-indent: 14px;
                //     z-index: 2;

                // }
            }
        }
    }
}
.btn1{
    font-size: 42px;
    color: white;
    margin-top: 4px;
}
.btn2{
    font-size: 42px;
    color: white;

    position: absolute;
    right: 6px;
    top:19px;
}
.pause{
    .minibar{
        div{
            &:first-child{
                img{
                    animation-play-state: paused;
                }
            }
            &:last-child{
                .nb{
                    span{
                        color: white;
                        font-size: 42px;
                    }
                    position: absolute;
                    width: 38px;
                    height: 38px;
                    border-radius: 50%;
                    background: #023a67;
                    // background: url(../assets/456.png);
                    // background-size: 100% 100%;
                }

            }
        }
    }
}
@keyframes round{
    from{
        transform: rotate(0deg);
    }
    to{
         transform: rotate(360deg);
    }
}
</style>