<template>
    <div :class="['bofang',{pause:!playstate}]"  >
        <div class="bg"></div>
        <img :src="playMusic.picUrl" class="imgbg"/>  

        <span @click="$emit('close')" class="back btn iconfont icon-fanhui"></span>
        <h2>{{playMusic.name}}</h2>
        <h3>{{playMusic.song.artists[0].name}}</h3>
        <div class="pic">
            <img :src="playMusic.picUrl"/>  
        </div>
        <div class="lyric" @click="wh">
            <ul v-bind:style="{
                transform:`translateY(-${lyricIndex*27-120}px)`
            }">
                <li v-for="(item,index) in lyricArray" :key="index" 
                :class="{highlight:index==(lyricIndex)}">
                    {{item.lyric}}
                </li>
            </ul>
        </div>
        <div class="control">
            <div class="playrange">
                <span>{{currentTime|formatTime}}</span>
                <!-- <div class="start"></div> -->
                <input type="range" max="100" 
                :value="(currentTime/duration*100)" 
                @change="changeTime"/>
                <span>{{duration|formatTime}}</span>
            </div>
            <span :class="{mode:playMode==1}" @click="$emit('changeMode',2)" class="btn lei xiao1 iconfont icon-shunxubofang"></span>
            <span :class="{mode:playMode==2}" @click="$emit('changeMode',3)" class="btn lei xiao1 iconfont icon-suijibofang"></span>
            <span :class="{mode:playMode==3}" @click="$emit('changeMode',1)" class="btn lei xiao1 iconfont icon-danquxunhuan"></span>


            <span @click="prev" class="btn iconfont icon-shangyishou"></span>
            <span @click="$emit('toggoPlay')" class="btn iconfont icon-zanting1" v-show="playstate"></span>
            <span @click="$emit('toggoPlay')" class="btn iconfont icon-bofang1" v-show="!playstate"></span>
            <span @click="next" class="btn iconfont icon-xiayishou"></span>
        
            <!-- <div @click="showcomment=!showcomment" class="gedan1 ping "> </div> -->

            <span @click="kaicomment()"  class="btn ping xiao1 iconfont icon-shousuoshangjiantou"></span>

            <span @click="showList=!showList" class="btn xiao1 gedan iconfont icon-liebiao2"></span>
            

                <div class="liebiaogedan">
                    <transition enter-active-class="animate__animated animate__slideInRight" leave-active-class="animate__animated animate__slideOutRight">

                    <ul v-show="showList" class="playlist">
                        <li v-for="(song,index) in playList" 
                        :key="index" 
                        @click="$emit('changePlayMusic',song)"
                        :class="{}"
                        ><span style="float:left" class="li1">{{song.name}}</span>
                        <span class="liebiaobo iconfont icon-bofang"></span>
                        </li>

                        <span @click="showList=!showList" class="btn  xiao1 gedan1 iconfont icon-changyongicon-"></span>

                    </ul>
                    </transition>

                </div>

                <div class="pinglun">
                    <transition enter-active-class="animate__animated animate__slideInUp" leave-active-class="animate__animated animate__slideOutDown">
                    <ul v-show="showcomment" class="commentlist">
                        <h1>歌曲评论</h1>

                        <li v-for="(item,index) in comentList" 
                        :key="index" 
                        >
                        <img :src="item.user.avatarUrl" >
                        <span style="">{{item.user.nickname}}</span>:
                        {{item.content}} 
                        </li>

                        <span @click="showcomment=!showcomment" class="btn  xiao1 gedan2 iconfont icon-shousuoxiajiantou"></span>

                    </ul>
                    </transition>

                </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"Bofang",
    data(){
        return{
            showList:false,
            showcomment:false,
            comentList:[],
            // timer:"",
            a:null,
            started:0,
        }
    },
    props:[
        "playMusic",
        "playstate",
        "currentTime",
        "lyric",
        "duration",
        "playList",
        "playMode"
    ],
    computed:{
        lyricArray(){
            let lyric=this.lyric;
            if(!lyric) return [];//如果没有歌词返回空
            let arr=lyric.split('\n');
            arr.pop();
            let reg=/\[(\d+):(\d+\.\d+)\](.*)/;
            let lyricArr=arr.map(d=>{
                if(reg.test(d)){
                    let lyricObj={
                        time:parseInt(RegExp.$1)*60+parseFloat(RegExp.$2),
                        lyric:RegExp.$3
                    }
                    return lyricObj;
                }
            });
            return lyricArr;
        },
        lyricIndex(){
            let ctime=this.currentTime;//当前播放时间
            let lyric=this.lyricArray;//歌词计算属性数组
            let index=0;
            for(let i=0;i<lyric.length;i++){
                if(i>=lyric.length-1){//最后一条歌词
                    index=i;
                    break;
                }
                if(lyric[i].time <= ctime && ctime<lyric[i+1].time){
                    index=i;
                    break;
                }
            }
            return index;
        }
    },
    filters:{
        formatTime(val){
            let minutes = parseInt(val/60);
            minutes=minutes<10?"0"+minutes:minutes;
            let sec = parseInt(val-parseInt(minutes*60));
            sec=sec<10?"0"+sec:sec;
            return minutes+":"+sec
        }
    },
    mounted() {
        // this.timer = setInterval(this.dingshiqi, 2000);
    },
    beforeDestroy() {
        // clearInterval(this.timer);
    },
    
    methods:{
        wh(){
            
            // console.log(this.lyricArray);
        },
        // dingshiqi(){
        //     // console.log(this.currentTime);
        //     // console.log(this.duration);
        //     // console.log(this.duration - this.currentTime);
        //     if(this.duration - this.currentTime<=1){
        //         if(this.playMode==3){
        //             // console.log(this.playstate)
        //             this.$emit('play')
        //         }else{
        //             this.next()
        //         }
        //     }

        // },
        kaicomment(){

            this.axios.get('/comment/music?id='+this.playMusic.id+'&limit=6').then(d=>{
                // console.log(d);
                this.showcomment=!this.showcomment;
                this.comentList= d.data.comments;
                // console.log(this.comentList)
            })
        },
        next(){
           let i=0;
           this.playList.map((d,index)=>{
               if(d.id==this.playMusic.id){//找到当前播放音乐下标
                    i=index;
               }
           });
            if(this.playMode==1){
                i++;//下一曲 判断是不是最后一首 
            }else if(this.playMode==2){
                i=Math.floor(Math.random()*this.playList.length);
            }
           let pmusic=this.playList[i];
           this.$emit('changePlayMusic',pmusic);
        },
        prev(){
            let i=0;
            this.playList.map((d,index)=>{
                if(d.id==this.playMusic.id){//找到当前播放音乐下标
                        i=index;
                }
            });
            if(this.playMode==1){
                i--;//上一曲 判断是不是第一曲
            }else if(this.playMode==2){
                i=Math.floor(Math.random()*this.playList.length);
            }


            let pmusic=this.playList[i];
            this.$emit('changePlayMusic',pmusic);
            //$emit('',song)
        },
        changeTime(event){
            let val = event.target.value;
            this.$emit('changetime',val);
        },



    }
}
</script>
