<template>
      <li class="item" @mouseenter="mouseenter1">
          <slot name="sortnum"></slot>
          <div class="left">
              <!-- 在v-html里调用过滤器 $options.filters.filtKeyword -->
              <h5 v-html="$options.filters.filtKeyword(item.name,keywords)"></h5>
              <p>
                  <i v-if="item.song.privilege&&item.song.privilege.maxbr>320000"></i>
                  <span v-for="artist in item.song.artists" :key="artist.id" v-html="$options.filters.filtKeyword(artist.name,keywords)"></span> - <span v-html="$options.filters.filtKeyword(item.song.album.name,keywords)"></span>
              </p>
            </div>
          <div class="right" @click.stop="$emit('play',item)">
              <loading v-if="playMusic&&item.id == playMusic.id"></loading>
          </div>
    </li>
</template>
<script>
import Loading from '@/components/Loading'
// import Loading from '@/components/Loading'

export default {
    props:{
        item:Object,
        keywords:{
            type:String,
            default:''
        },
        playMusic:{
            type:[Object,null]
        }
    },
    created(){
        // console.log(this.item)
        // console.log(this.keywords);
    },
    filters:{
        filtKeyword(value,k){
           return value.replace(k,'<span style="color:rgb(38, 72, 226);">'+k+'</span>')
        }
    },
    methods:{
        mouseenter1(){
            // console.log(this.target)
        }
    },
    components:{
        Loading
    }
}
</script>
<style lang="less" scoped>
    .text-hidden(){
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
        overflow: hidden;
    }
    li.item:hover{
        background: rgba(255, 255, 255, 0.3);
    }
    li.item{
        display: flex;
        align-items: center;
        padding:6px 10px 6px 0px;
        margin-left:10px;
        border-bottom: 1px solid rgba(0,0,0,.1);
        .left{
            flex:1;
            overflow: hidden;
            padding-right:10px;
           .text-hidden();
            i{
                display: inline-block;
                width: 12px;
                height: 8px;
                margin-right: 4px;
                background:url(../assets/index_icon_2x.png) no-repeat;
                background-size: 166px 97px;
            }
            h5{
                font-size:17px;
                color:#333;
                .text-hidden();
            }
            p{
                font-size:12px;
                color:rgb(0, 0, 0);
                .text-hidden();
            }
        }
        .right{
            width:22px;
            height:22px;
            color: white;
            background:url(../assets/index_icon_2x.png) no-repeat;
            background-size: 166px 97px;
            background-position: -24px 0;
        }
        &>b{
            display: inline-block;
            margin-right: 10px;
            font-weight: normal;
            font-size:17px;
            color:rgb(0, 0, 0);
        }
        &:first-child,&:nth-child(2),&:nth-child(3){
            &>b{
                color:#d1393c;
            }
        }
    }
</style>