import Vue from 'vue'
import VueRouter from 'vue-router'
import Recommend from '@/views/Recommend.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:"/recommend"
  },
  {
    path:"/recommend",
    name:"Recommend",
    component:Recommend
  },
  {
    path:'/recommend_songs/:id',
    // props:true,
    props:(oldroute)=>{
      return{
        id:parseInt(oldroute.params.id),
        // name:oldroute.params.name,
      }
    },
    meta:{
      hideNav:false
    },
    component:()=>import('@/views/RecommendSongs')
  },  
  {
    path: '/songitem/:id',
    name: 'songitem',
    props: true,
    component: () =>
        import ('../views/Songitem.vue'),
    meta: {
        hideNav: false
    },
  },
  {
    path:'/mv_sort/:id',
    // props:true,
    props:(oldroute)=>{
      return{
        id:parseInt(oldroute.params.id),
        // name:oldroute.params.name,
      }
    },
    meta:{
      hideNav:false
    },
    component:()=>import('@/views/MvSort')
  },
  {
    path:"/hot",
    name:"Hot",
    meta:{
      hideNav:false
    },
    component:()=>import('@/views/Hot')
  },
  {
    path:"/bang",
    name:"Bang",
    meta:{
      hideNav:false
    },
    component:()=>import('@/views/Bang')
  },
  {
    path:"/my",
    name:"My",
    component:()=>import('@/views/My')
  },
  {
    path:"/search",
    name:"Search",
    meta:{
      hideNav:false
    },
    component:()=>import('@/views/Search')
  }
]

const router = new VueRouter({
  routes
})

export default router
