<template>
  <div id="app">
      <!-- <Top></Top> -->
      <nav-bar v-show="!$route.meta.hideNav" class="wh"></nav-bar>
     <!-- <nav-bar v-show="showNav"></nav-bar> -->
    <div class="paddingTop" v-if="!$route.meta.hideNav"></div>

    <play 
    :playMusic="playMusic" 
    :playList="playList" 
    @changePlayMusic="changePlayMusic"
    ></play>
  <div class="main">
    <!-- <transition enter-active-class="animate__animated animate__slideInLeft" leave-active-class="animate__animated animate__slideOutRight"> -->
      <keep-alive>
        <router-view 
        @play="play"
        :playMusic="playMusic"
        class="index"
        />
    <div class="kong"> 1</div>

      </keep-alive>
    <!-- </transition> -->
  </div>
    <div class="paddingBottom" v-if="playMusic"></div>
  </div>
</template>
<script>
import NavBar from '@/components/NavBar'
import Play from '@/components/Play'
import defaultPic from '@/assets/default1.jpg'
export default {
  data(){
    return{
      playMusic:null,
      playList:[]
    }
  },
  components:{
    NavBar,Play
    // Top
  },
  methods:{
    changePlayMusic(event){
      if(event){
        this.playMusic=event;
      }
    },
    play(event){
      //判断歌曲对象里面是否有图片 没有就拿专辑的图片.
      if(!event.picUrl && event.song && event.song.album){
        event.picUrl=event.song.album.picUrl;
      }
      if(!event.picUrl){
        event.picUrl=defaultPic
      }
      this.playMusic=event;
      //加入歌单
      let isPush=true;
      this.playList.map(d=>{
          if(d.id==event.id){//歌曲已经存在
            isPush=false;
          }
      });
      if(isPush) this.playList.push(event);
    }
  },
  computed:{
    // showNav(){
    //   if(this.$route.name=="Hot"){
    //     return false;
    //   }
    //   return true;
    // }
  }
}
</script>
