<template>
  <div class="recommend">
    <border-title>推荐歌单</border-title>
    <div class="recommned_songs">
        <picture-item v-for="item in recommend_songs"
        :key="item.id"
        :item="item"
        ></picture-item>
    </div>
    <loading v-if="recommend_songs.length<=0"></loading>

    <border-title>MV排行</border-title>
    <div class="mv_sort">
        <picture-itemtwo v-for="mv in mv_sort" :key="mv.id"
          :item="mv"
        ></picture-itemtwo>
    </div>
    <border-title>最新音乐</border-title>
    <loading v-if="newSongs.length<=0"></loading>
    <ul class="kong">
      <img src="../assets/all.png" alt="">
      <li-item v-for="item in newSongs" @play="$emit('play',$event)" :item="item" :key="item.id" :playMusic="playMusic"></li-item>
    </ul>
    <!-- <border-title>Ituns飙升榜</border-title> -->
  </div>
</template>

<script>
import BorderTitle from '@/components/BorderTitle';
import PictureItem from '@/components/PictureItem'
import PictureItemtwo from '@/components/PictureItemtwo'
import LiItem from '@/components/LiItem'
import Loading from '@/components/Loading'
export default {
  name: 'Recommend',
  components: {
    BorderTitle,PictureItem,LiItem,Loading,PictureItemtwo
  },
  props:[
    "playMusic"
  ],
  data(){
    return {
      recommend_songs:[],
      mv_sort:[],
      newSongs:[]
    }
  },
  created(){
    this.$axios.get('/personalized?limit=100').then(d=>{
      this.recommend_songs=d.data.result.slice(0,6);
      // console.log(d);
    }).catch(()=>{
      console.log('网络连接错误');
    });
    //调用mv
    this.$axios.get('/top/mv?limit=10').then(d=>{
      // this.mv_sort=d.data
      this.mv_sort=d.data.data.slice(0,8).map(item=>{
        item.picUrl=item.cover;
        return item
      
      });
      console.log(d);

    });
    //最新音乐
    this.$axios.get('/personalized/newsong').then(d=>{
      this.newSongs=d.data.result
    }).catch(()=>{
      console.log('网络连接错误');
    })
  },
  methods:{
    whnb(){
      console.log(this.item.id);
    }
  }

}
</script>
